section#nav {
  min-height: 100px;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
}

section#nav .wrapper {
  display: flex;
  justify-content: space-between;
}

section#nav .logo {
  margin-top: 10px;
}

section#nav .logo a {
  text-decoration: none;
}

section#nav .logo p {
  text-align: center;
  color: #5363db;
  font-size: 2.4rem;
  font-family: 'Champagne & Limousines Bold';
  font-weight: normal;
}

section#nav .navigation ul li {
  list-style-type: none;
  display: inline-block;
  padding: 20px;
  padding-top: 37px;
}

section#nav .navigation a {
  color: #555555;
  font-size: 1.4rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  text-decoration: none;
}

section#nav .navigation a:hover {
  transition: 0.3s;
  color: #5363db;
}

section#nav .navigation a:hover:after {
  content: "";
  background-color: #5363db;
  display: block;
  width: 2.5rem;
  height: 1px;
  margin: 2px auto;
  transition: 0.3s;
}

section#nav .menu-link {
  z-index: 11;
  width: 50px;
  height: 50px;
  display: none;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
  position: absolute;
  right: 20px;
  top: 30px;
}

section#nav .menu-link span,
section#nav .menu-link span:before,
section#nav .menu-link span:after {
  display: block;
  height: 2px;
  width: 18px;
  right: 50%;
  margin-right: -9px;
  background-color: #333;
  position: absolute;
  top: 50%;
  margin-top: -1px;
}

section#nav .menu-link span:before {
  transition: all 0.2s;
  content: '';
  margin-top: -6px;
}

section#nav .menu-link span:after {
  transition: all 0.2s;
  content: '';
  margin-top: 4px;
}

section#nav .menu-link_active span {
  background-color: transparent;
}

section#nav .menu-link_active span:before {
  margin-top: 0;
  transform: rotate(135deg);
}

section#nav .menu-link_active span:after {
  margin-top: 0;
  transform: rotate(-135deg);
}

section#nav .menu {
  margin-top: -80px;
  opacity: 0.9;
  z-index: 9;
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: #7885e4;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -120vw;
  transition: all 0.4s;
}

section#nav .menu-active {
  right: 0;
}

section#nav .menu ul {
  text-align: center;
  list-style: none;
}

section#nav .menu ul li {
  width: 100%;
}

section#nav .menu ul a {
  font-size: 1.5rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  text-decoration: none;
  display: block;
  width: 110vw;
  font-weight: 100;
  letter-spacing: 4px;
  line-height: 4;
  color: #fff;
  transition: all 0.2s;
}

section#nav .menu ul a:hover {
  transition: all 0.2s;
  background-color: rgba(255, 255, 255, 0.4);
}

header {
  position: relative;
  z-index: 5;
  height: 96vh;
  min-height: 600px;
  background-repeat: no-repeat;
  background-position: center;
}

header .wrap {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #5465de;
  background-position: center;
  opacity: 0.7;
  width: 100%;
  height: 100%;
}

header .header-inf {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

header .send-form {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
  background-color: #fff;
  position: relative;
  top: 100px;
  width: 30rem;
  height: 39rem;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.15);
}

header .send-form h3 {
  margin-left: 2rem;
  padding-top: 3rem;
  color: #5363db;
  font-size: 2.564rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

header .send-form input {
  margin-left: 2rem;
  outline: none;
  margin-top: 1rem;
  padding-left: 1rem;
  border-radius: 8px;
  background-color: #f2f2f2;
  width: 23rem;
  height: 5rem;
  border: none;
  font-family: OpenSans;
  color: #888888;
  font-size: 1.4rem;
}

header .send-form input:focus:required:invalid {
  border: 1px solid red;
}

header .send-form input:focus:valid {
  border-color: 1px solid green;
}

header .send-form select {
  margin-left: 2rem;
  cursor: pointer;
  outline: none;
  margin-top: 1rem;
  padding-left: 1rem;
  border-radius: 8px;
  background-color: #f2f2f2;
  width: 24rem;
  height: 5rem;
  border: none;
  font-family: OpenSans;
  color: #888888;
  font-size: 1.4rem;
}

header .send-form .btn {
  margin-top: 4rem;
  margin-left: 9rem;
}

header .inf {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 30px);
  margin-left: 10rem;
  width: 48.5rem;
  position: relative;
  top: 160px;
}

header .inf h1 {
  color: #ffffff;
  font-size: 4.8rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

header .inf p {
  margin-top: 30px;
  font-family: 'Freestyle Script Normal', arial;
  font-weight: normal;
  font-size: 4rem;
  color: #fff;
  width: 30.8rem;
}

